body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 0.88rem;
  font-weight: 400;
  line-height: 1.5;
  color: #000000;
  text-align: left;
  background-color: #17A2B7
}

i {
  font-style: italic
}

.container {
  margin-top: 100px
}

.card {
  box-shadow: 0 0.46875rem 2.1875rem rgba(4, 9, 20, 0.03), 0 0.9375rem 1.40625rem rgba(4, 9, 20, 0.03), 0 0.25rem 0.53125rem rgba(4, 9, 20, 0.05), 0 0.125rem 0.1875rem rgba(4, 9, 20, 0.03);
  border-width: 0;
  transition: all .2s
}

.card-header:first-child {
  border-radius: calc(0.25rem - 1px) calc(0.25rem - 1px) 0 0
}

.card-header {
  display: flex;
  align-items: center;
  border-bottom-width: 1px;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0.625rem;
  height: 3.5rem;
  background-color: #fff
}

.todo-subheading {
  color: #858a8e;
  font-size: 10px
}

.card-header.card-header-tab .card-header-title {
  display: flex;
  align-items: center;
  white-space: nowrap
}

.card-header .header-icon {
  font-size: 1.65rem;
  margin-right: 0.625rem
}

.card-header.card-header-tab .card-header-title {
  display: flex;
  align-items: center;
  white-space: nowrap
}

.btn-actions-pane-right {
  margin-left: auto;
  white-space: nowrap
}

.text-capitalize {
  text-transform: capitalize !important
}

.scroll-area-sm {
  height: 288px;
  overflow-x: hidden
}

.list-group-item {
  position: relative;
  display: block;
  padding: 0.75rem 1.25rem;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.125);
  cursor: pointer;
}

.list-group {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0
}

.todo-indicator {
  position: absolute;
  width: 4px;
  height: 60%;
  border-radius: 0.3rem;
  left: 0.625rem;
  top: 20%;
  opacity: .6;
  transition: opacity .2s
}

.bg-warning {
  background-color: #f7b924 !important
}

.todo-content {
  padding: 1rem;
  flex-direction: row;
  align-items: center
}

.todo-content .todo-content-wrapper {
  display: flex;
  flex: 1;
  position: relative;
  align-items: center
}

.todo-content .todo-content-right.todo-content-actions {
  visibility: hidden;
  opacity: 0;
  transition: opacity .2s
}

.todo-content .todo-content-right {
  margin-left: auto
}

.btn:not(:disabled):not(.disabled) {
  cursor: pointer
}

.btn {
  position: relative;
  transition: color 0.15s, background-color 0.15s, border-color 0.15s, box-shadow 0.15s
}

.btn-done {
  color: #3ac47d;
  border-color: #3ac47d;
  height: 25px;
  width: 25px;
  padding: 0;
}

.btn-done:hover {
  color: #3ac47d;
  /* background-color: none; */
  border-color: #3ac47d
}

.btn-primary {
  color: #fff;
  background-color: #3f6ad8;
  border-color: #3f6ad8
}

.btn {
  position: relative;
  transition: color 0.15s, background-color 0.15s, border-color 0.15s, box-shadow 0.15s;
  outline: none !important
}

.card-footer {
  background-color: #fff
}

.mark-done .todo-heading  {
  text-decoration: line-through;
}
.blur-on-edit{
  filter: blur(5px);
}